  watch: {
    teachersList: {
      deep: true,
      handler() {
        this.filterPayouts();
      },
    },
    payoutRange: {
      deep: true,
      handler() {
        this.filterPayouts();
      },
    },
    setting: {
      deep: true,
      handler() {
        this.previewSetting();
      },
    },
    currentGroup(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.$emit("hidePreview");
        if (!this.isLoading) {
          this.initClick(this.currentGroup);
        }
      }
    },
    isLoading() {
      setTimeout(() => {
        this.initClick(this.currentGroup);
      }, 100);
    },
  },
  mounted() {
    let self = this;
    self.isLoading = false;

    setTimeout(() => {
      self.initClick(self.currentGroup);
      self.filterPayouts();
    }, 100);
  },
  methods: {
    scrollPayoutsList(e) {
      if (!this.hasScroll) {
        if (e.wheelDelta < 0) {
          if (!this.showNextMonthBtn) {
            $("#tkNextMonthBtn").stop().show().css("bottom", "-64px");
            $("#tkNextMonthBtn").stop().animate({
              bottom: 0,
            });
            this.showNextMonthBtn = true;
          }
        } else {
          $("#tkNextMonthBtn")
            .stop()
            .animate(
              {
                bottom: "-64px",
              },
              "fast",
              () => {
                $("#tkNextMonthBtn").stop().hide().css("bottom", "0");
              }
            );
          this.showNextMonthBtn = false;
        }
      } else {
        if (!this.showNextMonthBtn) {
          if (
            $("#payoutListContainer").scrollTop() +
              $("#payoutListContainer").innerHeight() >=
            $("#payoutListContainer")[0].scrollHeight
          ) {
            $("#tkNextMonthBtn").stop().css("bottom", "0").slideDown();
            this.showNextMonthBtn = true;
          }
        }
      }
    },
    getNextMonthInList() {
      let monthIndex = 0;
      this.monthsList.some((item, index) => {
        if (
          item.year == this.payoutRange.year &&
          item.month == this.payoutRange.month
        ) {
          monthIndex = index;
        }
      });
      if (this.monthsList.length == monthIndex + 1) {
        return 0;
      } else {
        let month = this.monthsList[monthIndex + 1];
        return this.getMonthStr(month.month) + " " + month.year;
      }
    },
    async filterPayouts() {
      this.monthsList.some((item) => {
        item.selected = false;
        if (
          item.year == this.payoutRange.year &&
          item.month == this.payoutRange.month
        ) {
          item.selected = true;
        }
      });

      await this.teachersList.some((item) => {
        this.upcomingPayoutsList.some((upcoming) => {
          upcoming.inRange = this.filterPayoutByDate(upcoming);
          if (item.userId == upcoming.userId) {
            upcoming.selected = item.selected;
          }
        });
        this.historyPayoutsList.some((history) => {
          history.inRange = this.filterPayoutByDate(history);
          if (item.userId == history.userId) {
            history.selected = item.selected;
          }
        });
      });

      let upcomingCount = $(".tk-payout-upcoming-item").length;
      let historyCount = $(".tk-payout-history-item").length;
      $(".tk-payout-upcoming-item").show()
      $(".tk-payout-history-item").show()

      if (upcomingCount == 0) {
        $("#upcomingSubTitle").hide();
      } else {
        $("#upcomingSubTitle").show();
      }

      if (historyCount == 0) {
        $("#historySubTitle").hide();
      } else {
        $("#historySubTitle").show();
      }

      this.noData = upcomingCount == 0 && historyCount == 0;

      $("#payoutListContainer").scrollTop(10); //控制滚动条下移
      this.hasScroll = $("#payoutListContainer").scrollTop() > 0;
      $("#payoutListContainer").scrollTop(0); //滚动条返回顶部
      $("#tkNextMonthBtn").stop().hide().css("bottom", "0");
      this.showNextMonthBtn = false;
    },
    filterPayoutByDate(item) {
      let arr = item.date.split("/");
      let year = arr[2],
        month = arr[0],
        date = arr[1];
      let inRange = false;
      if (this.payoutRange.year == year && this.payoutRange.month == month) {
        if (this.payoutRange.date > 0) {
          if (this.payoutRange.date == date) {
            inRange = true;
          }
        } else {
          inRange = true;
        }
      }
      return inRange;
    },
    getNameFromTeacherList(userId) {
      let name = "";
      this.teachersList.some((item) => {
        if (item.userId == userId) {
          name = item.name;
          return;
        }
      });
      return name;
    },
    selectDate(date, monthIndex, dateIndex) {
      $(".tk-payouts-date").removeClass("tk-payouts-date-select");
      $(".tk-payouts-date").eq(dateIndex).addClass("tk-payouts-date-select");
      this.payoutRange.date = date;
    },
    selectMonths(item, monthIndex) {
      this.monthsList.forEach((month) => {
        month.selected = false;
      });
      this.monthsList[monthIndex].selected = true;
      this.payoutRange.year = this.monthsList[monthIndex].year;
      this.payoutRange.month = this.monthsList[monthIndex].month;
      this.payoutRange.date = 0;
    },
    getMonthStr(month) {
      return this.$tools.getMonthStr(month);
    },
    selectPayout() {
      this.upcomingPayoutsList.forEach((item) => {
        item.preview = false;
      });
      this.historyPayoutsList.forEach((item) => {
        item.preview = false;
      });
      this.$emit("showStatement", {
        type: this.currentGroup,
      });
    },
    mousedownUserList(e) {
      this.mouseDownOnUserList = true;
      this.mouseDownOnUserListPositionX = e.clientX;
      this.userListMarginLeft = parseFloat(
        $("#payoutUserList").css("marginLeft").split("px")[0]
      );
    },
    mousemoveUserList(e) {
      if (this.mouseDownOnUserList) {
        this.mouseMoveOnUserList = true;
        let marginLeft = this.userListMarginLeft;
        marginLeft += e.clientX - this.mouseDownOnUserListPositionX;
        let width = $("#payoutList").width();
        if (Math.abs(marginLeft) + width >= $("#payoutUserList").width()) {
          marginLeft = width - $("#payoutUserList").width();
        }
        if (marginLeft >= 0) {
          marginLeft = 0;
        }
        // console.log(marginLeft)
        $("#payoutUserList").css({
          marginLeft: marginLeft,
        });
      }
    },
    mouseupUserList(e) {
      this.mouseDownOnUserList = false;
      this.mouseMoveOnUserList = false;
    },
    initClick(type) {
      let self = this;

      if (type === "invoices") {
        let invoiceList = document.getElementById("invoiceList");
        let invoice_child = invoiceList.getElementsByClassName("el-table__row");

        for (var i = 0; i < invoice_child.length; i++) {
          let index = i;
          invoice_child[i].onclick = () => {
            $(".tk-selected").removeClass("tk-selected");
            invoice_child[index].classList.add("tk-selected");
            self.preview(self.invoices[index], self.currentGroup);
          };
        }
      } else if (type === "payouts") {
      } else if (type === "income") {
      }
    },
    previewSetting() {
      let self = this;
      self.$emit("preview", {
        data: {
          salesTax:
            (parseFloat(self.setting.salesTax.simple) * 100).toFixed(2) + "%",
          lateFee: self.setting.lateFee.simple,
          notes: self.setting.invoiceNotes,
          emailText: self.setting.invoiceEmailText,
          receiptNotes: self.setting.receiptNotes,
          receiptEmailText: self.setting.receiptEmailText,
          schedule:
            self.setting.payoutScheduleSelects.simple !== "Monthly"
              ? self.setting.payoutScheduleSelects.simple
              : self.setting.dates.simple,
          onlinePayments: self.setting.onlinePayments,
        },
        type: self.currentGroup,
      });
    },
    initSettingData() {
      let self = this;
    },
    preview(data, type) {
      this.$emit("preview", {
        data: data,
        type: type,
      });
    },
    validationEmail(obj) {
      let self = this;
      if (self.$tools.isNull(obj.email)) {
        obj.isValid = true;
      } else {
        obj.isValid = self.$tools.isEmail(obj.email);
      }
    },
  },
};
